const chosenD = [
  "linear-gradient(135deg, rgb(255, 170, 133) 10%, rgb(179, 49, 95) 100%)",
  "linear-gradient(to right, rgb(213, 51, 105), rgb(203, 173, 109))",
  "linear-gradient(to right, rgb(242, 112, 156), rgb(255, 148, 114))",
  "linear-gradient(to right, rgb(19, 78, 94), rgb(113, 178, 128))",
  "linear-gradient(to right, rgb(189, 195, 199), rgb(44, 62, 80))",
];

const gradients = [site, chosenD].flat();

const root = document.querySelector(':root');

let current = 0;
const selected = [];

const show = (idx) => {
  if (idx < 0 || idx >= gradients.length) return;

  current = idx;
  root.style.setProperty('--gradient', gradients[current]);
};

const save = () => {
  const gradient = gradients[current];
  selected.push(gradient);

  console.log('Selected:', gradient, ', Total selected:', selected.length);
};

document.onkeydown = function (event) {
  if (event.key === 'ArrowLeft') show(current - 1);
  if (event.key === 'ArrowRight') show(current + 1);
  if (event.key === 'ArrowUp' || event.key === 'ArrowDown') {
    save();
  }
};
